import { SharedModule } from "app/shared/shared.module";
import { NgModule, NO_ERRORS_SCHEMA } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { ProductStatusComponent } from "app/stock/product-status/product-status.component";

export const routes: Routes = [{ path: "", component: ProductStatusComponent }];

@NgModule({
  imports: [SharedModule, RouterModule.forChild(routes)],
  declarations: [ProductStatusComponent],
  schemas: [NO_ERRORS_SCHEMA]
})
export class ProductStatusModule {}



